/** Logo **/
.logo-div {
  // float: left;
}


/** User Menu **/
.main-header h2, .drowsy-main-menu h2 {
  display: none;
}

.main-header ul, .drowsy-main-menu ul {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-top: 12px;
  padding-right: 30px;
  float: right;
  color: #FFF;
}

.main-header li, .drowsy-main-menu li {
  display: inline;
}

.drowsy-user-menu {
  border: solid 1px #000;
  // background-color: black;
}

.drowsy-user-menu .menu a:link,
.drowsy-user-menu .menu a:visited,
.drowsy-user-menu .menu a:active {
  color:#FFF; text-decoration: none;
}

.drowsy-user-menu .menu a:hover {
  color:#FFF; text-decoration: underline;
}


